@import '../../../../styles/vars';
@import '../../../../styles/mixins';

.otherDays {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(2, auto);
  gap: 15px;

  width: 100%;

  @include tablet {
    grid-template-columns: 130px 1fr;
  }

  @include mobile {
    grid-template-columns: 1fr;
  }
}


